import { useEffect, useState, useRef } from "react";
import { subscribeBackgroundColor, csi } from "../lib/utils/cep";

interface LogEntry {
  id: number;
  timestamp: string;
  level: "log" | "info" | "warn" | "error";
  message: string;
  data?: any;
  source?: string;
}

export const DebugPanel = () => {
  const [bgColor, setBgColor] = useState("#282c34");
  const [logs, setLogs] = useState<LogEntry[]>([]);
  const [filter, setFilter] = useState<"all" | "log" | "info" | "warn" | "error">("all");
  const [autoScroll, setAutoScroll] = useState(true);
  const logsEndRef = useRef<HTMLDivElement>(null);
  const logIdCounter = useRef(0);

  useEffect(() => {
    if (!window.cep) return;

    const handleLogEvent = (event: any) => {
      try {
        console.log("Received log event:", event);
        console.log("Event data type:", typeof event.data);
        console.log("Event data:", event.data);

        let data;
        if (typeof event.data === 'string') {
          data = JSON.parse(event.data);
        } else {
          data = event.data;
        }

        console.log("Parsed data:", data);

        const logEntry: LogEntry = {
          id: logIdCounter.current++,
          timestamp: new Date(data.timestamp).toLocaleTimeString("ko-KR", {
            hour: "2-digit",
            minute: "2-digit",
            second: "2-digit",
            fractionalSecondDigits: 3,
          }),
          level: data.level,
          message: data.args.join(" "),
          source: data.source,
        };
        setLogs((prev) => [...prev, logEntry]);
      } catch (error) {
        console.error("Error parsing log event:", error, event);
      }
    };

    csi.addEventListener("com.lasker.studio.log", handleLogEvent);

    return () => {
      csi.removeEventListener("com.lasker.studio.log", handleLogEvent);
    };
  }, []);

  useEffect(() => {
    const originalConsole = {
      log: console.log,
      info: console.info,
      warn: console.warn,
      error: console.error,
    };

    const createLogEntry = (level: LogEntry["level"], args: any[], source = "debug"): LogEntry => {
      const timestamp = new Date().toLocaleTimeString("ko-KR", {
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
        fractionalSecondDigits: 3,
      });

      let message = "";
      let data = undefined;

      if (args.length === 1) {
        if (typeof args[0] === "string") {
          message = args[0];
        } else {
          message = "";
          data = args[0];
        }
      } else {
        message = args
          .map((arg) => {
            if (typeof arg === "object") {
              try {
                return JSON.stringify(arg, null, 2);
              } catch {
                return String(arg);
              }
            }
            return String(arg);
          })
          .join(" ");
      }

      return {
        id: logIdCounter.current++,
        timestamp,
        level,
        message,
        data,
        source,
      };
    };

    console.log = (...args: any[]) => {
      originalConsole.log(...args);
      setLogs((prev) => [...prev, createLogEntry("log", args)]);
    };

    console.info = (...args: any[]) => {
      originalConsole.info(...args);
      setLogs((prev) => [...prev, createLogEntry("info", args)]);
    };

    console.warn = (...args: any[]) => {
      originalConsole.warn(...args);
      setLogs((prev) => [...prev, createLogEntry("warn", args)]);
    };

    console.error = (...args: any[]) => {
      originalConsole.error(...args);
      setLogs((prev) => [...prev, createLogEntry("error", args)]);
    };

    // Initial log
    console.log("Debug Panel initialized");

    return () => {
      console.log = originalConsole.log;
      console.info = originalConsole.info;
      console.warn = originalConsole.warn;
      console.error = originalConsole.error;
    };
  }, []);

  useEffect(() => {
    if (autoScroll && logsEndRef.current) {
      logsEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [logs, autoScroll]);

  useEffect(() => {
    if (window.cep) {
      subscribeBackgroundColor(setBgColor);
    }
  }, []);

  const filteredLogs = logs.filter((log) => {
    if (filter === "all") return true;
    return log.level === filter;
  });

  const clearLogs = () => {
    setLogs([]);
    console.log("Logs cleared");
  };

  const exportLogs = () => {
    const dataStr = JSON.stringify(logs, null, 2);
    const dataBlob = new Blob([dataStr], { type: "application/json" });
    const url = URL.createObjectURL(dataBlob);
    const link = document.createElement("a");
    link.href = url;
    link.download = `debug_logs_${Date.now()}.json`;
    link.click();
    URL.revokeObjectURL(url);
  };

  const getLogIcon = (level: LogEntry["level"]) => {
    switch (level) {
      case "error":
        return "‚ùå";
      case "warn":
        return "‚ö†Ô∏è";
      case "info":
        return "‚ÑπÔ∏è";
      default:
        return "üìù";
    }
  };

  const stats = {
    total: logs.length,
    log: logs.filter((l) => l.level === "log").length,
    info: logs.filter((l) => l.level === "info").length,
    warn: logs.filter((l) => l.level === "warn").length,
    error: logs.filter((l) => l.level === "error").length,
  };

  return (
    <div className="min-h-screen flex flex-col font-mono text-xs" style={{ backgroundColor: bgColor }}>
      <div className="p-4 pb-3.5 border-b border-white/10 bg-black/20">
        <h1 className="m-0 mb-3.5 text-lg text-[#61dafb]">üêõ Debug Console</h1>

        <div className="flex flex-col gap-2.5">
          <div className="flex gap-2 flex-wrap items-center">
            <button
              className={`px-3 py-1.5 border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 ${
                filter === "all"
                  ? "bg-[#61dafb] text-[#282c34] border-[#61dafb] font-semibold"
                  : "bg-white/5 text-[#ccc] hover:bg-white/10 hover:border-white/20"
              }`}
              onClick={() => setFilter("all")}
            >
              All ({stats.total})
            </button>
            <button
              className={`px-3 py-1.5 border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 ${
                filter === "log"
                  ? "bg-[#61dafb] text-[#282c34] border-[#61dafb] font-semibold"
                  : "bg-white/5 text-[#ccc] hover:bg-white/10 hover:border-white/20"
              }`}
              onClick={() => setFilter("log")}
            >
              üìù Log ({stats.log})
            </button>
            <button
              className={`px-3 py-1.5 border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 ${
                filter === "info"
                  ? "bg-[#61dafb] text-[#282c34] border-[#61dafb] font-semibold"
                  : "bg-white/5 text-[#ccc] hover:bg-white/10 hover:border-white/20"
              }`}
              onClick={() => setFilter("info")}
            >
              ‚ÑπÔ∏è Info ({stats.info})
            </button>
            <button
              className={`px-3 py-1.5 border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 ${
                filter === "warn"
                  ? "bg-[#61dafb] text-[#282c34] border-[#61dafb] font-semibold"
                  : "bg-white/5 text-[#ccc] hover:bg-white/10 hover:border-white/20"
              }`}
              onClick={() => setFilter("warn")}
            >
              ‚ö†Ô∏è Warn ({stats.warn})
            </button>
            <button
              className={`px-3 py-1.5 border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 ${
                filter === "error"
                  ? "bg-[#61dafb] text-[#282c34] border-[#61dafb] font-semibold"
                  : "bg-white/5 text-[#ccc] hover:bg-white/10 hover:border-white/20"
              }`}
              onClick={() => setFilter("error")}
            >
              ‚ùå Error ({stats.error})
            </button>
          </div>

          <div className="flex gap-2 flex-wrap items-center">
            <label className="flex items-center gap-1.5 text-[#ccc] cursor-pointer select-none">
              <input
                type="checkbox"
                checked={autoScroll}
                onChange={(e) => setAutoScroll(e.target.checked)}
                className="cursor-pointer"
              />
              Auto Scroll
            </label>
            <button onClick={exportLogs} className="px-3 py-1.5 bg-white/5 text-[#ccc] border border-white/10 rounded cursor-pointer text-[11px] transition-all duration-200 hover:bg-white/10 hover:border-white/20">üíæ Export</button>
            <button onClick={clearLogs} className="px-3 py-1.5 bg-[#ff3b30]/10 text-[#ff3b30] border border-[#ff3b30]/30 rounded cursor-pointer text-[11px] transition-all duration-200 hover:bg-[#ff3b30]/20">
              üóëÔ∏è Clear
            </button>
          </div>
        </div>
      </div>

      <div className="flex-1 overflow-y-auto p-2.5 bg-black/10">
        {filteredLogs.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-full min-h-[200px] text-[#666]">
            <p className="my-1.5">No logs to display</p>
            <p className="text-[11px] text-[#555] my-1.5">Console output will appear here</p>
          </div>
        ) : (
          filteredLogs.map((log) => (
            <div key={log.id} className={`flex gap-2 p-2 px-2.5 mb-0.5 border-l-[3px] bg-white/[0.02] transition-all duration-100 hover:bg-white/[0.05] ${
              log.level === 'log' ? 'border-l-[#999]' :
              log.level === 'info' ? 'border-l-[#61dafb] bg-[#61dafb]/[0.03]' :
              log.level === 'warn' ? 'border-l-[#ffcc00] bg-[#ffcc00]/[0.03]' :
              'border-l-[#ff3b30] bg-[#ff3b30]/[0.05]'
            }`}>
              <span className="flex-shrink-0 text-sm">{getLogIcon(log.level)}</span>
              <span className="flex-shrink-0 text-[#666] text-[10px] min-w-[80px]">{log.timestamp}</span>
              <span className={`flex-shrink-0 font-semibold text-[10px] min-w-[50px] ${
                log.level === 'log' ? 'text-[#999]' :
                log.level === 'info' ? 'text-[#61dafb]' :
                log.level === 'warn' ? 'text-[#ffcc00]' :
                'text-[#ff3b30]'
              }`}>[{log.level.toUpperCase()}]</span>
              {log.source && <span className="flex-shrink-0 font-semibold text-[10px] text-[#9966ff] min-w-[60px] bg-[#9966ff]/10 px-1.5 py-0.5 rounded">[{log.source}]</span>}
              <div className="flex-1 min-w-0">
                {log.message && <div className="text-[#ddd] break-words whitespace-pre-wrap">{log.message}</div>}
                {log.data && (
                  <pre className="mt-1.5 p-2 bg-black/30 rounded text-[#a5d6ff] text-[11px] overflow-x-auto border border-white/5">{JSON.stringify(log.data, null, 2)}</pre>
                )}
              </div>
            </div>
          ))
        )}
        <div ref={logsEndRef} />
      </div>
    </div>
  );
};
